@media only screen and (max-width:1300px) {
    * {
        padding: 0;
        margin: 0;
    }

    .navmiddle {
        display: none;
        margin: 0;
    }

    .navmiddle li {
        flex-direction: column;
        align-items: self-start;
        position: absolute;
        left: 0;
        top: -20px;
        background-color: black;
        width: 50%;
        height: 100vh;
        z-index: 12;
        border-right: 0.5px solid red;
        text-align: left;
        row-gap: 10px;
        border-bottom-right-radius: 40px;
    }

    .navfrm input {
        display: none;
    }

    .navfrm .input-clicked {
        display: block;
    }

    .fa-solid {
        padding: 10px;
        font-size: 1.5rem;
    }

    .navright {
        position: fixed;
        width: 100%;
    }

    .navright img {
        height: 40px;
    }

    .navfrm {
        position: absolute;
        right: 7px;
        background: none;
        margin-right: 0;
    }

    /* //responsive search */
    .navfrm2 {
        position: absolute;
        right: 7px;
        top: 57px;
        margin-right: 0;
        display: flex;
        background: #000000;
        padding: 2px 10px;
        border-radius: 15px;
    }

    .navfrm2 .input2 {
        display: block;
        outline: none;
        background-color: #000000;
        border: none;
        color: white;
    }

    .searchbtn2 {
        background: black;
        border: none;
    }

    .navright h3 {
        margin-left: 1px
    }

    .burger {
        display: block;
        position: absolute;
    }

    .displaynone {
        display: none;
    }

    .displayblock {
        display: block;
    }

    .dop2 {
        display: none;
    }

    .dop1 {
        display: none;
    }

    .dropdown:hover .dop2 {
        display: none;
    }

    .dropdown:hover .dop1 {
        display: none;
    }

    .dff {
        display: block;
    }

    .df {
        display: none;
    }

    .dropdown:hover .dropdown-content {
        display: none;
    }

    .navmiddle .title {
        color: red;
    }

    .navmiddle a:hover {
        color: rgb(209, 209, 209);
        border-bottom: none;
    }

    .dropdown-content a:hover {
        background-color: none;
        border-radius: 0;
    }

    .dropdown:hover .dropdown-content {
        display: none;
    }

    .burger2 {
        left: 36%;
        position: absolute;
        z-index: 12;
    }

    .yoo img {
        height: 200px;
    }

    .bit {
        font-size: 15px;
    }

    .mx-4 {
        margin-right: 1rem !important;
        margin-left: 1rem !important;
    }

    .yeahhoo {
        display: flex;
        flex-direction: column;
        margin-top: 20px;
    }

    .slide1 img {
        width: 100%;
        height: auto;
    }

    .slider h4 {
        font-size: 18px;
        bottom: 40px;
        top:auto;
    }
    .slider-container {
        height: auto;
    }
    footer{
        padding: 0;
    }
    footer p{
        font-size: 10px;
    }
}

@media only screen and (max-width: 1400px) {
    .body2 {
        display: none !important;
    }
}

@media(max-width:980px) and (min-width:600px) {
    .slider h4 {
        font-size: 18px;
        bottom: 40px;
    }
}

@media only screen and (max-width: 600px) {
    .slider h4 {
        font-size: 12px;
        bottom: 40px;
    }
}

@media(max-width:374px) {
    .slider h4 {
        font-size: 14px;
        bottom: 25px;
    }
}