@import url('https://fonts.googleapis.com/css2?family=Bree+Serif&family=Poppins&family=Ubuntu&display=swap');

* {
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
}

body {
  background-color: rgb(0, 0, 0);
  font-family: 'Bree Serif', serif;
  font-family: 'Poppins', sans-serif;
  font-family: 'Ubuntu', sans-serif;
}

.badges {
  position: absolute;
  z-index: 1;
  display: flex;
  right: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

nav {
  display: flex;
  align-items: center;
  background-color: black;
  height: 60px;
  position: sticky;
  z-index: 12;
  top: 0;
  left: 0;
}

.navright {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
}

.navright img {
  height: 50px;
  filter: invert(1);
}

.navright h3 {
  margin-left: 12px;
  margin-bottom: 0;
}

.navmiddle {
  margin: auto;
}

.navmiddle a {
  text-decoration: none;
  padding: 2px 16px;
  color: white;
  font-size: 1rem;
}

.navmiddle a:hover {
  color: rgb(209, 209, 209);
  border-bottom: 2px solid red;
}

.navmiddle li {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navfrm {
  margin-right: 35px;
  background: #545454;
  display: flex;
  align-items: center;
  padding: 2px 7px;
  border-radius: 5px;
  position: absolute;
  right: 0;
}

.navfrm2 {
  display: none;
}

.navfrm input {
  outline: none;
  background-color: #545454;
  border: none;
  color: white;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #000000;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  margin-top: 2px;
  z-index: 11;
  border-radius: 12px;
  text-align: center;
}

.dropdown-content a {
  color: rgb(185, 185, 185);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  /* background-color: #000000; */
  /* border-radius: 12px; */
  color: red;
  border-bottom: none;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.df {
  display: flex;
  align-items: center;
  justify-content: center;
}

.dop2 {
  display: none;
}

.dropdown:hover .dop2 {
  display: inline-block;
}

.dropdown:hover .dop1 {
  display: none;
}

.burger {
  position: absolute;
  left: 15px;
  display: none;
}

.line {
  border: 2px solid rgb(255, 255, 255);
  background-color: rgb(255, 255, 255);
  padding: 0 14px;
  margin: 4px 0;
}

.burger2 {
  position: absolute;
  left: 15px;
  display: none;
}

.line2 {
  border: 2px solid rgb(255, 255, 255);
  background-color: rgb(255, 255, 255);
  padding: 01px 14px;
  margin: 0px 0;
  transform: rotate(45deg);
}

.line3 {
  border: 2px solid rgb(255, 255, 255);
  background-color: rgb(255, 255, 255);
  padding: 1px 14px;
  margin: -4.5px 0;
  transform: rotate(135deg);
  margin-bottom: 0px;
}

.searchbtn {
  background: none;
  border: none;
}


.dff a {
  font-size: 13px;
}

.dff {
  display: flex;
  flex-direction: column;
  display: none;
}

.dff1 {
  display: flex;
  flex-direction: column;
  align-items: self-start;
}

.gg {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 3px;
  background-color: rgb(102 102 102 / 50%);
  right: 0px;
  top: -23px;
  position: absolute;
}

.bit {
  font-size: 23px;
  margin-top: 11px;
  margin-bottom: 0;
  text-align: initial;
}

ul {
  margin-bottom: 0;
}

.border2 {
  border: 2px solid red;
}

.yoo {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.blur {
  filter: blur(2px);
}

.playbutton {
  position: absolute;
  font-size: 2.5rem;
}

.displaynoneglobal {
  display: none;
}

.displaynoneblockglobal {
  display: block;
}

.anchor {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  text-decoration: none;
  z-index: 10;
  background-color: #FFF;
  opacity: 0;
  filter: alpha(opacity=1);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.body2 {
  width: 20%;
}

.F-div {
  padding: 26px 0px;
}

.Snd-div {
  background: #222222;
  padding: 25px 30px;
  font-size: 1.5rem;
  color: red;
  cursor: pointer;
}

.Fth-div {
  font-size: 1rem;
  margin-bottom: 19px;
  margin-top: -13px;
  text-align: start;
}

.badges-body3 {
  top: 94px;
  right: 90px;
}

.accbtn {
  background: rgb(32, 32, 32);
  outline: none;
  border: none;
  color: white;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
  width: 204px;
  height: 45px;
}

.collapsible-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
}

.collapsible-content.open {
  max-height: 300px;
  transition: max-height 0.5s ease-in;
}

.ptag li {
  list-style: none;
  background: #000000;
  margin: 1px 0;
}

.ptag li a {
  color: white;
  text-decoration: none;
}

.slider-container {
  overflow: hidden;
  width: 100%;
  background-color: black;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  border-bottom: 1px solid #ac0000;
  height: 370px;
}

.slider {
  display: flex;
  transition: transform 1s ease-in-out;
}

.slide1 {
  flex: 0 0 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 0;
  left: 0;
}

.slide1 img {
  width: 90%;
  height: 100%;
  filter: brightness(0.4);

}

.slide1 h4 {
  position: absolute;
  font-size: 2rem;
  /* bottom: 30px; */
  top: 295px;
}

.pa {
  position: absolute;
  bottom: 20px;
  color: white;
}

.span2 {
  display: none;
}
footer p{
  font-size: 0.9rem;
}